

:root {
    --main-color: rgba(0, 0, 0, 0.3);
}

img {
    width: 100%;
    z-index: 10;
}

.form {
    margin-left: 40%;
    margin-top: 20px;

}

.button {
    display: block;
    margin-top: 15px;
    margin-left: 50px;
}

.weather {
    font-family: 'Roboto', sans-serif;
    color: rgba(0, 0, 0, 0.3);
    background-color: #7A956B;
    border-radius: 10px;
    padding: 20px;
    max-width: 450px;
    margin: 10px auto;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.weather__loading {
    display: flex;
    justify-content: center;
    align-items: center;
}

.weather__loading img {
    max-width: 50px; 
 } 

.weather__header {
    display: flex;
}

.weather__main {
flex: 1 1 auto;
}

.weather__city {
    color: var(--main-color);
    font-size: 40px;
    font-weight: 700;
    margin-bottom: 15px;
}

.weather__status {
    color: var(--main-color);
    font-size: 30px;
    margin-bottom: 20px;
}

.weather__temp {
    color: var(--main-color);
    font-size: 30px;
    font-weight: 700;
    margin-bottom: 10px;
}

.weather__feels-like {
    color: var(--main-color);
        font-size: 14px;
        font-weight: 700;
}
.weather__temp::after,
.weather__feels-like::after {
    color: var(--main-color);
    display: inline-block;
    margin-left: 5px;
    content: "*C";
}